//@todo rearange notes after dragged

/**
 * Defines a [[DataItem]] for [[SankeyDiagram]].
 * 
 * @see {@link DataItem}
 */
export class SankeyDiagramDataItem extends ChartDataItem {

	/**
	 * Defines a type of [[Component]] this data item is used for.
	 *
	 * @ignore Exclude from docs
	 * @type {SankeyDiagram}
	 */
	public _component: SankeyDiagram;

	/**
	 * An a link element, connecting two nodes.
	 * 
	 * @type {SankeyLink}
	 */
	protected _link: SankeyLink;

	/**
	 * An origin node.
	 * 
	 * @type {SankeyNode}
	 */
	public fromNode: SankeyNode;

	/**
	 * A destination node.
	 * 
	 * @type {SankeyNode}
	 */
	public toNode: SankeyNode;

	/**
	 * Constructor
	 */
	constructor() {
		super();
		this.className = "SankeyDiagramDataItem";

		this.values.value = {};

		this.applyTheme();
	}

	/**
	 * Source node's name.
	 * 
	 * @param {string}  value  Name
	 */
	public set fromName(value: string) {
		this.setProperty("fromName", value);
	}

	/**
	 * @return {string} name
	 */
	public get fromName(): string {
		return this.properties.fromName;
	}

	/**
	 * Destination node's name.
	 * 
	 * @param {string}  value  Name
	 */
	public set toName(value: string) {
		this.setProperty("toName", value);
	}

	/**
	 * @return {string} name
	 */
	public get toName(): string {
		return this.properties.toName;
	}


	/**
	 * Node color
	 * 
	 * @param {string}  value  Name
	 */
	public set color(value: Color) {
		this.setProperty("color", toColor(value));
	}

	/**
	 * @return {string} color
	 */
	public get color(): Color {
		return this.properties.color;
	}

	/**
	 * Link's value.
	 * 
	 * @param {number}  value  Value
	 */
	public set value(value: number) {
		this.setValue("value", value);
	}

	/**
	 * @return {number} Value
	 */
	public get value(): number {
		return this.values["value"].value;
	}

	/**
	 * A visual element, representing link between the source and target nodes.
	 *
	 * Link's actual thickness will be determined by `value` of this link and
	 * `value` of the source node.
	 *
	 * @readonly
	 * @return {SankeyLink} Link element
	 */
	public get link(): SankeyLink {
		if (!this._link) {
			this._link = this.component.links.create();
			this.addSprite(this._link);
		}
		return this._link;
	}

}


