export interface ISankeyDiagramAdapters extends IChartAdapters, ISankeyDiagramProperties { }


/**
 * ============================================================================
 * MAIN CLASS
 * ============================================================================
 * @hidden
 */

/**
 * Creates a Pie chart
 * @see {@link ISankeyDiagramEvents} for a list of available Events
 * @see {@link ISankeyDiagramAdapters} for a list of available Adapters
 * @important
 */
export class SankeyDiagram extends Chart {

	/**
	 * A Color Set to use when applying/generating colors for each subsequent
	 * node.
	 *
	 * @type {ColorSet}
	 */
	public colors: ColorSet = new ColorSet();

	/**
	 * Defines a type for the DataItem.
	 *
	 * @ignore Exclude from docs
	 * @type {SankeyDiagramDataItem}
	 */
	public _dataItem: SankeyDiagramDataItem;

	/**
	 * Defines available data fields.
	 *
	 * @ignore Exclude from docs
	 * @type {ISankeyDiagramDataFields}
	 */
	public _dataFields: ISankeyDiagramDataFields;

	/**
	 * Defines available properties.
	 *
	 * @ignore Exclude from docs
	 * @type {ISankeyDiagramProperties}
	 */
	public _properties: ISankeyDiagramProperties;

	/**
	 * Defines available adapters.
	 *
	 * @ignore Exclude from docs
	 * @type {SeriesAdapters}
	 */
	public _adapter: ISankeyDiagramAdapters;

	/**
	 * Event dispatcher.
	 * 
	 * @type {SpriteEventDispatcher<AMEvent<SankeyDiagram, ISankeyDiagramEvents>>} Event dispatcher instance
	 */
	public events: SpriteEventDispatcher<AMEvent<SankeyDiagram, ISankeyDiagramEvents>>;

	/**
	 * A list of chart's Sankey nodes.
	 * 
	 * @param {DictionaryTemplate<string, SankeyNode>}
	 */
	public nodes: DictionaryTemplate<string, SankeyNode> = new DictionaryTemplate<string, SankeyNode>(new SankeyNode());

	/**
	 * A list of Sankey links connecting nodes.
	 * 
	 * @param {ListTemplate<SankeyLink>}
	 */
	public links: ListTemplate<SankeyLink> = new ListTemplate<SankeyLink>(new SankeyLink());

	/**
	 * @todo Description
	 */
	protected _levelSum: { [index: number]: number };

	/**
	 * @todo Description
	 */
	protected _levelNodesCount: { [index: number]: number };

	/**
	 * [maxSum description]
	 *
	 * @ignore Exclude from docs
	 * @todo Description
	 * @type {number}
	 */
	public maxSum: number;

	/**
	 * number of nodes in level with max value, needed for position calculation
	 */
	protected maxSumLevelNodeCount:number;

	/**
	 * [valueHeight description]
	 *
	 * @ignore Exclude from docs
	 * @todo Description
	 * @type {number}
	 */
	public valueHeight: number = 0;

	/**
	 * A total number of levels, present on this chart.
	 * 
	 * @type {number}
	 */
	protected _levelCount: number;

	/**
	 * A container that holds all of the link elements.
	 * 
	 * @type {Container}
	 */
	public linksContainer: Container;

	/**
	 * A container that holds all of the node elements.
	 * @type {Container}
	 */
	public nodesContainer: Container;

	/**
	 * Sorted nodes iterator.
	 * 
	 * @ignore
	 * @type {Iterator}
	 */
	protected _sorted: $iter.Iterator<[string, SankeyNode]>;

	/**
	 * Alignment of nodes
	 * 
	 * @ignore
	 * @type {"top" | "bottom" | "middle"}
	 */
	protected _nodeAlign: "top" | "bottom" | "middle";

	/**
	 * Constructor
	 */
	constructor() {

		// Init
		super();
		this.className = "SankeyDiagram";
		this.nodePadding = 20;
		this.sortBy = "none";
		this.orientation = "horizontal";
		this.sequencedInterpolation = true;
		this.nodeAlign = "middle";

		this.colors.step = 2;

		let linksContainer = this.chartContainer.createChild(Container);
		linksContainer.shouldClone = false;
		linksContainer.width = percent(100);
		linksContainer.height = percent(100);
		linksContainer.layout = "none";
		linksContainer.isMeasured = false;
		this.linksContainer = linksContainer;
		this.linksContainer.id = "linksContainer"


		let nodesContainer = this.chartContainer.createChild(Container);
		nodesContainer.shouldClone = false;
		nodesContainer.width = percent(100);
		nodesContainer.height = percent(100);
		nodesContainer.layout = "none";
		nodesContainer.isMeasured = false;
		this.nodesContainer = nodesContainer;

		// Apply theme
		this.applyTheme();
	}

	/**
	 * (Re)validates chart's data, effectively causing the chart to redraw.
	 *
	 * @ignore Exclude from docs
	 */
	public validateData(): void {
		super.validateData();

		// reset toNodes and fromNodes
		$iter.each(this.nodes.iterator(), (strNode) => {
			let node = strNode[1];
			node.incomingDataItems.clear();
			node.outgoingDataItems.clear();
		})

		// build blocks
		$iter.each(this.dataItems.iterator(), (dataItem) => {
			let fromName = dataItem.fromName;

			if (fromName) {
				let node = this.nodes.getKey(fromName);
				if (!node) {
					node = this.nodes.create(fromName);
					node.name = fromName;
					node.chart = this;
				}

				dataItem.addSprite(node);
				dataItem.fromNode = node;
				dataItem.fromNode.outgoingDataItems.push(dataItem);
			}
			let toName = dataItem.toName;
			if (toName) {
				let node = this.nodes.getKey(toName);
				if (!node) {
					node = this.nodes.create(toName);
					node.name = toName;
					node.chart = this;
					if (!node.dataItem) {
						dataItem.addSprite(node);
					}
				}
				dataItem.toNode = node;
				dataItem.toNode.incomingDataItems.push(dataItem);
			}

			if (!dataItem.fromNode) {
				let strokeModifier = new LinearGradientModifier();
				strokeModifier.opacities = [0, 1];
				dataItem.link.strokeModifier = strokeModifier;
			}

			if (!dataItem.toNode) {
				let fillModifier = new LinearGradientModifier();
				fillModifier.opacities = [1, 0];
				dataItem.link.strokeModifier = fillModifier;
			}
		});


		this._levelCount = 0;

		$iter.each(this.nodes.iterator(), (strNode) => {
			let node = strNode[1];

			if (node.fill instanceof Color) {
				node.color = node.fill;
			}

			if (node.color == undefined) {
				node.color = this.colors.next();
			}

			if (node.dataItem.color != undefined) {
				node.color = node.dataItem.color;
			}
			node.level = this.getNodeLevel(node, 0);

			this._levelCount = $math.max(this._levelCount, node.level);
		});

		this.sortNodes();

		if (this.interpolationDuration > 0) {
			this.events.once("validated", this.appear, this);
		}
	}

	/**
	 * Returns node's highest level.
	 * 
	 * @param  {SankeyNode}  node   Node
	 * @param  {number}      level  Current level
	 * @return {number}             New level
	 */
	protected getNodeLevel(node: SankeyNode, level: number): number {
		let levels: number[] = [level];
		$iter.each(node.incomingDataItems.iterator(), (link: SankeyDiagramDataItem) => {
			if (link.fromNode) {
				levels.push(this.getNodeLevel(link.fromNode, level + 1));
			}
		})
		return Math.max(...levels);
	}

	/**
	 * Sorts nodes by either their values or names, based on `sortBy` setting.
	 */
	protected sortNodes() {
		if (this.sortBy == "name") {
			this._sorted = this.nodes.sortedIterator();
		}
		else if (this.sortBy == "value") {
			this._sorted = $iter.sort(this.nodes.iterator(), (x, y) => $order.reverse($number.order(x[1].value, y[1].value)));
		}
		else {
			this._sorted = this.nodes.iterator();
		}
	}

	/**
	 * Calculates relation between pixel height and total value.
	 *
	 * In Sankey the actual thickness of links and height of nodes will depend
	 * on their values.
	 */
	protected calculateValueHeight() {
		//@todo respect startIndex/endIndex?
		// calculate values of each node
		$iter.each(this.nodes.iterator(), (strNode) => {
			this.getNodeValue(strNode[1]);
		})

		// calculate sums of each level
		this._levelSum = {};
		this._levelNodesCount = {};
		this.maxSum = 0;

		$iter.each(this.nodes.iterator(), (strNode) => {
			let node = strNode[1];
			let level = node.level;

			if ($type.isNumber(this._levelSum[level])) {
				this._levelSum[level] += node.value;
			}
			else {
				this._levelSum[level] = node.value;
			}

			if ($type.isNumber(this._levelNodesCount[level])) {
				this._levelNodesCount[level]++;
			}
			else {
				this._levelNodesCount[level] = 1;
			}
		});

		let maxSumLevel: number;
		for (let key in this._levelSum) {
			if (this.maxSum < this._levelSum[key]) {
				this.maxSum = this._levelSum[key];
				maxSumLevel = Number(key);
			}
		}
		let maxSumLevelNodeCount = this._levelNodesCount[maxSumLevel];

		let availableHeight: number;
		if (this.orientation == "horizontal") {
			availableHeight = this.chartContainer.maxHeight - 1;
		}
		else {
			availableHeight = this.chartContainer.maxWidth - 1;
		}
		this.valueHeight = (availableHeight - (maxSumLevelNodeCount - 1) * this.nodePadding) / this.maxSum;

		this.maxSumLevelNodeCount = maxSumLevelNodeCount;
	}

	/**
	 * Updates a cummulative value of the node.
	 *
	 * A node's value is determined by summing values of all of the incoming
	 * links or all of the outgoing links, whichever results in bigger number.
	 * 
	 * @param {SankeyNode}  node  Node value
	 */
	protected getNodeValue(node: SankeyNode) {
		let fromSum = 0;
		let toSum = 0;
		$iter.each(node.incomingDataItems.iterator(), (link: SankeyDiagramDataItem) => {
			fromSum += link.value;
		});

		$iter.each(node.outgoingDataItems.iterator(), (link: SankeyDiagramDataItem) => {
			toSum += link.value;
		});

		node.value = $math.max(fromSum, toSum)
	};

	/**
	 * Redraws the chart.
	 *
	 * @ignore Exclude from docs
	 */
	public validate(): void {
		super.validate();
		this.calculateValueHeight();
		let container = this.nodesContainer;
		container.removeChildren();

		let nextCoordinate: { [index: number]: number } = {};

		let nodesInLevel: number[] = [];
		$iter.each(this._sorted, (strNode) => {
			let node = strNode[1];
			let level = node.level;
			if (!$type.isNumber(nodesInLevel[level])) {
				nodesInLevel[level] = 1;
			}
			else {
				nodesInLevel[level]++;
			}
		});

		let maxSumLevelNodeCount = this.maxSumLevelNodeCount;

		$iter.each(this._sorted, (strNode) => {
			let node = strNode[1];
			let level = node.level;

			let levelCoordinate: number = 0;


			switch (this.nodeAlign) {
				case "bottom":
					levelCoordinate = (this.maxSum - this._levelSum[level]) * this.valueHeight - (nodesInLevel[level] - maxSumLevelNodeCount) * this.nodePadding;
					break;
				case "middle":
					levelCoordinate = (this.maxSum - this._levelSum[level]) * this.valueHeight / 2 - (nodesInLevel[level] - maxSumLevelNodeCount) * this.nodePadding / 2;
					break;
			}


			if (node.value > 0) {
				node.parent = container;
			}

			let delta: number;
			let x: number;
			let y: number;

			if (this.orientation == "horizontal") {
				delta = (container.pixelWidth - node.pixelWidth) / this._levelCount;

				x = delta * node.level;
				y = nextCoordinate[level] || levelCoordinate;

				let h = node.value * this.valueHeight;

				node.height = h
				node.minX = x;
				node.maxX = x;
				nextCoordinate[level] = y + h + this.nodePadding;
			}
			else {
				delta = (container.pixelHeight - node.pixelHeight) / this._levelCount;

				x = nextCoordinate[level] || levelCoordinate;
				y = delta * node.level;

				let w = node.value * this.valueHeight;

				node.width = w;
				node.minY = y;
				node.maxY = y;
				nextCoordinate[level] = x + w + this.nodePadding;
			}

			node.x = x;
			node.y = y;
		});
	}

	/**
	 * [appear description]
	 *
	 * @ignore Exclude from docs
	 * @todo Description
	 */
	public appear(): void {

		let container = this.nodesContainer;

		let i: number = 0;

		$iter.each(this.links.iterator(), (link) => {
			link.hide(0);
		})

		$iter.each(this._sorted, (strNode) => {

			let node = strNode[1];
			let property: "dx" | "dy";

			if (this.orientation == "horizontal") {
				node.dx = -(container.pixelWidth - node.pixelWidth) / this._levelCount;
				property = "dx";
			}
			else {
				node.dy = -(container.pixelHeight - node.pixelHeight) / this._levelCount;
				property = "dy";
			}

			let delay = 0;
			let duration = this.interpolationDuration;
			if (this.sequencedInterpolation) {
				delay = this.sequencedInterpolationDelay * i + duration * i / $iter.length(this.nodes.iterator());
			}

			node.opacity = 0;
			node.invalidateLinks();

			node.animate([{ property: "opacity", from: 0, to: 1 }, { property: property, to: 0 }], this.interpolationDuration, this.interpolationEasing).delay(delay);

			$iter.each(node.outgoingDataItems.iterator(), (dataItem) => {
				dataItem.link.show(this.interpolationDuration).delay(delay);
			})

			i++;
		});
	}

	/**
	 * Changes the sort type of the nodes.
	 *
	 * This will actually reshuffle nodes using nice animation.
	 */
	protected changeSorting() {
		this.sortNodes();

		let nextCoordinate: { [index: number]: number } = {};

		$iter.each(this._sorted, (strNode) => {
			let node = strNode[1];
			let level = node.level;

			let levelCoordinate = (this.maxSum - this._levelSum[level]) * this.valueHeight / 2;

			let property: "x" | "y";
			let nodeHeight: number;
			if (this.orientation == "horizontal") {
				property = "y";
				nodeHeight = node.pixelHeight;
			}
			else {
				property = "x";
				nodeHeight = node.pixelWidth;
			}

			node.animate({ property: property, to: nextCoordinate[level] || levelCoordinate }, this.interpolationDuration, this.interpolationEasing);
			nextCoordinate[level] = (nextCoordinate[level] || levelCoordinate) + nodeHeight + this.nodePadding;

			node.invalidateLinks();
		});
	}

	/**
	 * Sets defaults that instantiate some objects that rely on parent, so they
	 * cannot be set in constructor.
	 */
	protected applyInternalDefaults(): void {

		super.applyInternalDefaults();

		// Add a default screen reader title for accessibility
		// This will be overridden in screen reader if there are any `titles` set
		if (!$type.hasValue(this.readerTitle)) {
			this.readerTitle = this.language.translate("Sankey diagram");
		}
	}

	/**
	 * Creates and returns a new data item.
	 * 
	 * @return {this} Data item
	 */
	protected createDataItem(): this["_dataItem"] {
		return new SankeyDiagramDataItem();
	}

	/**
	 * Padding for node square in pixels.
	 *
	 * Padding will add extra space around node's name label.
	 * 
	 * @param {number} value Padding (px)
	 */
	public set nodePadding(value: number) {
		this.setPropertyValue("nodePadding", value, true);
	}

	/**
	 * @return {number} Padding (px)
	 */
	public get nodePadding(): number {
		return this.getPropertyValue("nodePadding");
	}

	/**
	 * Sort nodes by "name" or "value" or do not sort at all. If not sorted, nodes will appear in the same order as they are in the data.
	 * @default "none"
	 * @param {"none" "name" | "value"}  value  Node sorting
	 */
	public set sortBy(value: "none" | "name" | "value") {
		this.setPropertyValue("sortBy", value);
		this.changeSorting();
	}

	/**
	 * @returns {"none" | name" | "value"} Node sorting
	 */
	public get sortBy(): "none" | "name" | "value" {
		return this.getPropertyValue("sortBy");
	}

	/**
	 * How to align nodes. In case layout is vertical, top means left and bottom means right
	 * 
	 * @param {"top" | "middle" | "bottom"}  value  Node sorting
	 */
	public set nodeAlign(value: "top" | "middle" | "bottom") {
		this.setPropertyValue("nodeAlign", value);
		this.changeSorting();
	}

	/**
	 * @returns {"top" | "middle" | "bottom"} Returns nodeAlign value
	 */
	public get nodeAlign(): "top" | "middle" | "bottom" {
		return this.getPropertyValue("nodeAlign");
	}

	/**
	 * Orientation of the chart: "horizontal" or "vertical";
	 * 
	 * @param {Orientation} value Orientation
	 */
	public set orientation(value: Orientation) {
		this.setPropertyValue("orientation", value, true);
		let nameLabel = this.nodes.template.nameLabel;
		if (value == "vertical") {
			nameLabel.label.horizontalCenter = "middle";
			nameLabel.locationX = 0.5;
		}
		else {
			nameLabel.label.horizontalCenter = "left";
			nameLabel.locationX = 1;			
		}
	}

	/**
	 * @return {Orientation} Orientation
	 */
	public get orientation(): Orientation {
		return this.getPropertyValue("orientation");
	}

}

/**
 * Register class in system, so that it can be instantiated using its name from
 * anywhere.
 * 
 * @ignore
 */
registry.registeredClasses["SankeyDiagram"] = SankeyDiagram;


